import { Component } from '@angular/core';
import { ViewChild } from '@angular/core';
import { OnChanges } from '@angular/core';
import { NgZone } from '@angular/core';
import { OnInit } from '@angular/core';
import { ElementRef } from '@angular/core';
import { HostListener } from '@angular/core';

import * as THREE from 'three';

import { Cubelet } from './model/Cubelet';

@Component({
    selector: 'graphics',
    templateUrl: './graphics.component.html',
    styleUrls: ['./graphics.component.css']
})
export class GraphicsComponent implements OnInit, OnChanges {
    @ViewChild('myCanvas') 
    canvasRef: ElementRef;

    private lastUpdate;

    private scene: THREE.Scene;
    private camera: THREE.PerspectiveCamera;
    private renderer: THREE.WebGLRenderer;

    private cubelet: Cubelet;

    private FOV = 75;
    private WIDTH = 500;
    private HEIGHT = 500;
    private NEAR = 0.1;
    private FAR = 1000;

    constructor(private ngZone: NgZone) {
    }

    ngOnInit() {

        document.addEventListener("visibilitychange", () => {
            if (document.hidden) {
                console.log("hidden");
            } else {
                console.log("visible");
                this.lastUpdate = Date.now();
            }
        });

        this.lastUpdate = Date.now();

        this.scene = new THREE.Scene();
        this.camera = new THREE.PerspectiveCamera(this.FOV, 
                                                  this.WIDTH / this.HEIGHT, 
                                                  this.NEAR, 
                                                  this.FAR);
        this.renderer = new THREE.WebGLRenderer();

        this.renderer.setSize(this.WIDTH, this.HEIGHT);
        this.canvasRef.nativeElement.appendChild(this.renderer.domElement);

        var light = new THREE.PointLight(0xffffff);
        light.position.x = 2;
        light.position.y = 1;
        light.position.z = 10;

        this.scene.add(light);

        this.scene.add(this.cubelet.mesh);

        this.camera.position.z = 5;

        this.ngZone.runOutsideAngular(() => this.paint());
    }

    ngOnDestroy() {
    }

    ngOnChanges() {

    }

    private paint() {
        var now = Date.now();
        var dt = now - this.lastUpdate;
        this.lastUpdate = now;

        this.cubelet.mesh.rotation.x += 0.01;
        this.cubelet.mesh.rotation.y += 0.01;
        this.renderer.render(this.scene, this.camera);

        requestAnimationFrame(() => this.paint());
    }

}


